.menu-button {
  appearance: none;
  background: transparent;
  width: 50px;
  height: 50px;
  position: fixed;
  left: 0.25%;
  top: 20%;
  border: none;
  border-radius: 50%;
  border-color: rgba(0, 0, 0, 0.8);
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 103;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.menu-button img {
  width: 20px;
  transform: rotate(180deg);
}

@media only screen and (max-width: 668px) {
  .menu-button {
    width: 40px;
    height: 40px;
  }

  .menu-button img {
    width: 15px;
  }
}
