.search-input {
  height: 100%;
  @include flex-box(null, null, center, center);
  position: relative;
}

.search-input__inp {
  font-size: 18px;
  padding: 8px;
  margin: 25px 30px 0 10px;
  border-radius: 5px;
  border: none;
  border-style: solid;
  border-color: rgb(226, 210, 210);
  border-width: 0px;
  background: rgba(0, 0, 0, 0.6);
  color: #fff;
}

.search-input__img {
  margin: 13px 40px;
  position: absolute;
  width: 20px;
  right: 0;
  top: 30%;
}

@media only screen and (max-width: 668px) {
  .search-input__inp {
    margin: 25px 30px 10px;
    padding: 5px;
    background: rgba(0, 0, 0, 0.7);
  }

  .search-input__inp {
    &:focus {
      border: 1px solid #fff !important;
    }
  }
  .search-input__img {
    margin: 7px 40px;
    position: absolute;
    width: 20px;
    right: 0;
  }
}
